}
*,
*::before,
*::after {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
body {
  min-width: 320px;
  margin: 0;
  font-family: "BloggerSans", sans-serif;
  color: #000000;
  background-color: #ffffff;
  overflow-x: hidden;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.centering-wrapper {
  position: relative;
  margin: 0 auto;
  width: 320px;
}
.centering-wrapper--flex {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.page-main {
  position: relative;
}
.page-catalog {
  background-color: #edeff6;
}
.hide-block {
  display: none;
}
.show-block {
  display: block;
}
.show-flex {
  display: flex;
}
.show-absolute {
  position: absolute;
  display: block;
}
.show-fixed {
  position: fixed;
}
.button {
  position: relative;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-top: 23px;
  padding-bottom: 22px;
  padding-left: 24px;
  font-family: "BloggerSans", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  color: #1d2e5b;
  text-transform: none;
  background-color: #ffd74b;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.button--bordered {
  border: 3px solid #192144;
  padding-top: 20px;
  padding-bottom: 19px;
  padding-left: 21px;
}
.button::after {
  content: "";
  position: absolute;
  top: 24px;
  right: 22px;
  display: block;
  width: 10px;
  height: 14px;
  background-image: url("../../img/triangle-arrow-right.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.button--bordered::after {
  top: 22px;
  right: 20px;
}
.button:hover,
.button:focus {
  background-color: #ffbd30;
}
.button:active {
  color: rgba(29, 46, 91, 0.3);
  background-color: #ffbd30;
}
.button:active::after {
  opacity: 0.3;
  background-color: #ff8d30;
}
.button-round {
  display: block;
  width: 100%;
  height: 100%;
  text-align: center;
  padding-top: 7px;
  text-transform: uppercase;
  font-family: "BloggerSans", sans-serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 30px;
  color: #161c36;
  border: none;
  background-color: #ffd74b;
  border-radius: 20px;
  cursor: pointer;
}
.button-round--wide {
  width: 100%;
}
.button-round:hover,
.button-round:focus {
  background-color: #ffbd30;
}
.button-round:active {
  color: rgba(22, 28, 53, 0.3);
  background-color: #ffbd30;
}
.button-round--white {
  background-color: #ffffff;
  border: none;
}
.button-round--grey {
  background-color: #dfe3f0;
  border: none;
}
.button-round--close {
  padding-left: 30px;
  background-color: #d6daed;
  background-image: url("../../img/filter-close.svg");
  background-repeat: no-repeat;
  background-position: top 15px left 84px;
  border: none;
}
@media (min-width: 768px) {
  .centering-wrapper {
    width: 768px;
  }
  .button {
    padding-top: 33px;
    padding-bottom: 31px;
    padding-left: 34px;
    font-size: 24px;
    text-align: left;
    border-radius: 20px;
  }
  .button::after {
    top: 34px;
    right: 35px;
  }
  .button-round {
    padding-top: 13px;
    padding-bottom: 13px;
    font-size: 20px;
    border-radius: 25px;
  }
  .button-round--close {
    padding-top: 37px;
    padding-bottom: 33px;
    padding-left: 42px;
    background-position: top 40px left 244px;
    background-size: 18px 18px;
  }
}
@media (min-width: 1440px) {
  .centering-wrapper {
    width: 1440px;
  }
}
